___CSS_LOADER_EXPORT___.push([module.id, `.overlay {
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: black;
  opacity: 0.6;
}

.ship-placer-container {
  position: absolute;
  z-index: 2;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.ship-placer {
  height: max(40%, 500px);
  background-color: var(--ship-placer-bg);
  border-radius: 10px;
  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 20px;
  padding: 1vw 2vw;
}

.ship-placer h2 {
  font-size: 1.8rem;
}

.ship-placer p {
  font-size: 1.3rem;
}

.ship-placer p i {
  font-size: 1.5rem;
  font-weight: bold;
  text-decoration: underline;
}

.ship-placer .game-board {
  --cell-size: 30px;
  margin: auto;
}

.ship-placer .game-board .cell {
  --cell-size: 30px;
}

.hovered-carrier {
  position: relative;
  background-color: var(--hovered-ship-bg);
  pointer-events: none;
  width: 150px;
}

.hovered-battleship {
  position: relative;
  background-color: var(--hovered-ship-bg);
  pointer-events: none;
  width: 120px;
}

.hovered-destroyer {
  position: relative;
  background-color: var(--hovered-ship-bg);
  pointer-events: none;
  width: 90px;
}

.hovered-submarine {
  position: relative;
  background-color: var(--hovered-ship-bg);
  pointer-events: none;
  width: 90px;
}

.hovered-patrolBoat {
  position: relative;
  background-color: var(--hovered-ship-bg);
  pointer-events: none;
  width: 60px;
}

.hidden {
  top: 0;
  left: 0;
  pointer-events: none !important;
  position: absolute !important;
  max-height: 0 !important;
  opacity: 0 !important;
}
